/* Footer.css */
.footer {
    background-color: #1a1a1a;
    color: #ffffff;
    font-family: 'Arial', sans-serif;
}

.footer-logo {
    height: 50px;
    width: 50px;
    border-radius: 50%;
    object-fit: cover;
    border: 2px solid #ffffff;
}

.footer-heading {
    color: #ffffff;
    font-size: 1.2rem;
    font-weight: 600;
    position: relative;
    padding-bottom: 10px;
}

.footer-heading::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 50px;
    height: 2px;
    background-color: #e63946;
}

.footer-links li a,
.footer-link {
    color: #adb5bd;
    text-decoration: none;
    transition: all 0.3s ease;
}

.footer-links li a:hover,
.footer-link:hover {
    color: #ffffff;
    padding-left: 5px;
}

.footer-contact li {
    color: #adb5bd;
}

.footer-contact svg {
    color: #e63946;
    min-width: 20px;
}

.social-icons {
    display: flex;
}

.social-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    color: white;
    font-size: 1.2rem;
    transition: all 0.3s ease;
}

.social-icon.youtube {
    background-color: #ff0000;
}

.social-icon.instagram {
    background: linear-gradient(45deg, #405de6, #5851db, #833ab4, #c13584, #e1306c, #fd1d1d);
}

.social-icon.facebook {
    background-color: #3b5998;
}

.social-icon:hover {
    transform: translateY(-3px);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

.newsletter-form .form-control {
    background-color: #2d2d2d;
    border: none;
    color: white;
}

.newsletter-form .form-control:focus {
    box-shadow: 0 0 0 0.25rem rgba(230, 57, 70, 0.25);
}

.newsletter-form .btn-primary {
    background-color: #e63946;
    border: none;
}

.newsletter-form .btn-primary:hover {
    background-color: #c1121f;
}

.footer-bottom {
    font-size: 0.9rem;
}

/* Responsive */
@media (max-width: 768px) {
    .footer-heading {
        margin-top: 20px;
    }
    
    .footer-links,
    .footer-contact {
        margin-bottom: 20px;
    }
    
    .footer-bottom div {
        text-align: center !important;
        margin-bottom: 10px;
    }
}